@import "../../stylesheets/_variables.css";

:local .Home {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-rows: 1fr;
  -ms-flex-align: center;
      align-items: center;
}

:local .Contents {
  text-align: center;
}

:local .Portrait {
  border-radius: 50%;
  width: 300px;
  height: 300px;
  overflow: hidden;
  margin: 0 auto;
}

:local .Portrait img {
  height: 100%;
  width: 300px;
}

:local .Portrait:hover {
  -webkit-transform: scale(1.025);
      -ms-transform: scale(1.025);
          transform: scale(1.025);
}

:local .Logo {
  text-align: center;
}



